<script>
import jsPDF from "jspdf";
import "jspdf-autotable";
export default {
  name: "REPORTE",
  props:{
       Tipo: {
      type: String,
      default: "REPORTE DE FALTAS"
    }
  },
  data() {
    return {
      
      nombre_doc:"",
      Cabezera: ["DOCENTE", "FECHA", "OBSERVACION"],
      Cuerpo: [],
      Profesores_Seleccionados: [],
      Selec_Prof: false,
      Lista_Profesores: [],
      Lista_Profesores_2: [],
      selection_Fecha: 1,
      Texto:"",
      Todos: true,
      PDF: false,
      hoy: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      modal: false,
      modal2: false,
      Dia: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      Dias: [
        new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
      ],
      Objeto_Quincenas: [],
      Lista_Quincena: [],
      Selecion_Quincena: "",
      search: "",
      Justificacion: {
        Falta: "Enfermedad",
        Dia: "01-03-2021",
        noEstatus: -1,
        Estatus: "Negado",
      },
      info: false,
      headers: [
        {
          text: "PROFESOR",
          align: "start",
          value: "Nombre",
        },
        { text: "FECHA", value: "Dia" },
        { text: "OBSERVACIONES", value: "Falta" },
      ],

      docentes: [
        {
          Nombre: "Juanito Perez",
          Falta: "Enfermedad",
          Dia: "01-03-2021",
          noEstatus: -1,
          Estatus: "Negado",
          Fechas: "12-04-2021",
          Fecha_Opcion: 0,
          Justi: "Fui al medico en la clinica 234",
          Resp: "No procede por que en el articulo 90" + "A".repeat(2000),
        },
        {
          Nombre: "Juanito Perez",
          Falta: "Da Economico",
          noEstatus: 0,
          Dia: "01-06-2021",
          Estatus: "En Espera",
          Fechas: ["12-04-2021", "12-05-2021", "13-04-2021"],
          Fecha_Opcion: 2,
          Justi: "",
        },
        {
          Nombre: "Rogelio Perez Garzas",
          Falta: "Da Economico",
          noEstatus: 0,
          Dia: "01-06-2021",
          Estatus: "En Espera",
          Fechas: ["12-04-2021", "12-05-2021", "13-04-2021"],
          Fecha_Opcion: 2,
          Justi: "",
        },
      ],
    };
  },
  watch: {
    selection_Fecha: function (value) {
      this.Cargar_docentes_para_seleccion();
      if (value == 0) {
        this.Quincenas();
      }
    },
    Todos: function (value) {
      if (value == false) {
        this.Selec_Prof = true;
      }
    },
  },
  methods: {
    Quincenas() {
      if (this.Lista_Quincena.length == 0) {
        let Meses = [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre",
        ];
        let hoy = new Date();
        let Ultimo_mes = hoy.getMonth();
        let mes = hoy.getMonth();

        if (hoy.getDate() < 15) {
          Ultimo_mes--;
        }
        if (Ultimo_mes == -1) {
          return [];
        } else {
          for (let i = 0; i <= Ultimo_mes; i++) {
            let primero = new Date(hoy.getFullYear(), i, 1);
            let ultimo = new Date(hoy.getFullYear(), i + 1, 0);
            let quince = new Date(hoy.getFullYear(), i, 15);
            let siguiente = new Date(hoy.getFullYear(), i, 16);
            if (i != mes) {
              this.Objeto_Quincenas.push({
                quincena: "1a Quincena de " + Meses[i],
                primer_dia: primero,
                ultimo_dia: quince,
              });
              this.Lista_Quincena.push("1a Quincena de " + Meses[i]);
              this.Lista_Quincena.push("2da Quincena de " + Meses[i]);
              this.Objeto_Quincenas.push({
                quincena: "2da Quincena de " + Meses[i],
                primer_dia: siguiente,
                ultimo_dia: ultimo,
              });
            } else {
              this.Objeto_Quincenas.push({
                quincena: "1a Quincena de " + Meses[i],
                primer_dia: primero,
                ultimo_dia: quince,
              });
              this.Lista_Quincena.push("1a Quincena de " + Meses[i]);
            }
          }
        }
      }
      console.log(this.Selecion_Quincena);
    },
    Cargar_docentes_para_seleccion() {
      this.docentes.forEach((element) => {
        if (this.Lista_Profesores.length == 0) {
          this.Lista_Profesores.push(element.Nombre);
        } else if (!(this.Lista_Profesores.includes(element.Nombre))) {
          this.Lista_Profesores.push(element.Nombre);
        }
      });
    },
    Cargar_Datos_Para_PDF() {      
      this.Cuerpo = [];
      if (this.Todos) {        
        this.docentes.forEach((element) => {
          this.Cuerpo.push([element.Nombre,element.Dia,element.Falta])          
        });
      }else{
        console.log(this.Profesores_Seleccionados);
        this.docentes.forEach((element) => {          
          if (this.Profesores_Seleccionados.includes(element.Nombre)) {
          this.Cuerpo.push([element.Nombre,element.Dia,element.Falta])
          } 
        });
      }
      this.Texto="";
      if (this.selection_Fecha==0) {
        this.Texto = "Periodo Escolar 21-22/1 "+this.Selecion_Quincena+"."        
      }else if(this.selection_Fecha==1){
        this.Texto = "Del Dia: "+this.Dia+"."
      }else{
        this.Texto = "Del Dia: "+this.Dias[0]+" al "+this.Dias[1]+"."
      }
      this.nombre_doc = this.Texto.replace(" ","")
      console.log(this.Texto);
    },
    Crear_PDF() {
      this.Cargar_Datos_Para_PDF();
      var ipn =
        "data:image/png;base64,";
      var imgdata =
        "data:image/png;base64,";
      var doc = new jsPDF();      
      doc.autoTable({
        margin: { top: 50 },
        startY: 60,
        head: [this.Cabezera],
        body: 
          this.Cuerpo
        ,
        didDrawPage: function () {
          // Header
          doc.addImage(ipn, "PNG", 0, 10, 40, 30);
          doc.addImage(imgdata, "PNG", 158, 10, 40, 30);
          doc.setFontSize(18);
          doc.setFont("Times", "bold");
          doc.text("INSTITUTO POLITCNICO NACIONAL", 35, 20);
          doc.text("ESCUELA SUPERIOR DE CMPUTO", 39, 26);
          doc.setFont("Times", "BoldItalic");
          doc.setFontSize(15);
          doc.text("SUBDIRECCIN ADMINISTRATIVA", 50, 33); //#282828
          doc.setFontSize(20);
          
          console.log(doc.getTextColor());
        },
      });
      doc.setPage(1);
      doc.setTextColor("#005CFE");
      console.log("HOLA");
      doc.setFont("Times", "bold");
      doc.text(this.Tipo,60,45);
      console.log("HOLA");
      doc.setTextColor(0);
      doc.setFontSize(12);
      doc.text(this.Texto,40,55);
      console.log("HOLA");
      doc.save(this.nombre_doc+".pdf");
    },
  },
};
</script>